.Home {
  &__fields {
    width: 100%;
    margin: 5px;
    display: flex;
    justify-content: space-between;
  }

  &__field {
    border: #e0e0e0;
    box-shadow: 0px 0px 2px 0px;
    padding: 8px;
    border-radius: 4px;
    background: #ffffff;
    &--text{
      width: 240px;
    }
  }
}
